@charset "UTF-8";
.CodeMirror {
  height: auto;
  min-height: 300px;
  border: 1px solid #ddd;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  padding: 10px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Hiragino Sans GB", "Microsoft Yahei", "微软雅黑", Arial, Helvetica, STHeiti, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 18px;
  line-height: 1.2;
  color: #333;
  z-index: 1; }

.CodeMirror-scroll {
  min-height: 300px;
  margin-bottom: -30px;
  margin-right: -30px;
  padding-bottom: 30px; }

.CodeMirror-fullscreen {
  background: #fff;
  position: fixed !important;
  top: 58px;
  left: 0;
  right: 0;
  bottom: 0;
  height: auto;
  z-index: 9;
  border: 0;
  border-top: 1px solid #ddd; }

.CodeMirror-sided {
  width: 50% !important; }

.CodeMirror pre {
  padding: 0 4px; }

.CodeMirror .CodeMirror-code .cm-tag {
  color: #40bf40; }

.CodeMirror .CodeMirror-code .cm-attribute {
  color: #40bf40; }

.CodeMirror .CodeMirror-code .cm-string {
  color: #777; }

.CodeMirror .CodeMirror-code .cm-link {
  color: #0080ff; }

.CodeMirror .CodeMirror-code .cm-url {
  color: #777; }

.CodeMirror .CodeMirror-code .CodeMirror-selected {
  background: rgba(0, 0, 0, 0.1); }

.CodeMirror .CodeMirror-code .cm-formatting-header,
.CodeMirror .CodeMirror-code .cm-formatting-em,
.CodeMirror .CodeMirror-code .cm-formatting-strong,
.CodeMirror .CodeMirror-code .cm-formatting-strikethrough,
.CodeMirror .CodeMirror-code .cm-formatting-code,
.CodeMirror .CodeMirror-code .cm-formatting-code-block,
.CodeMirror .CodeMirror-code .cm-hr {
  color: #777; }

.CodeMirror .CodeMirror-code .cm-header-1,
.CodeMirror .CodeMirror-code .cm-header-2,
.CodeMirror .CodeMirror-code .cm-header-3,
.CodeMirror .CodeMirror-code .cm-header-4,
.CodeMirror .CodeMirror-code .cm-header-5,
.CodeMirror .CodeMirror-code .cm-header-6 {
  line-height: 2; }

.CodeMirror .CodeMirror-code .cm-header-1 {
  font-size: 32px; }

.CodeMirror .CodeMirror-code .cm-header-2 {
  font-size: 26px; }

.CodeMirror .CodeMirror-code .cm-header-3 {
  font-size: 24px; }

.CodeMirror .CodeMirror-code .cm-header-4 {
  font-size: 22px; }

.CodeMirror .CodeMirror-code .cm-header-5 {
  font-size: 20px; }

.CodeMirror .CodeMirror-code .cm-header-6 {
  font-size: 18px; }

.CodeMirror .CodeMirror-code .cm-header,
.CodeMirror .CodeMirror-code .cm-strong {
  font-weight: 600; }

.CodeMirror .CodeMirror-code .cm-comment {
  font-family: Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  background: rgba(0, 0, 0, 0.05);
  font-size: 13px;
  padding: 6px 0 3px; }

.CodeMirror .CodeMirror-code span.CodeMirror-selectedtext.cm-comment {
  background: rgba(0, 0, 0, 0.05); }

.CodeMirror .CodeMirror-code .cm-quote {
  color: #777; }

.CodeMirror .CodeMirror-code .cm-strikethrough {
  text-decoration: line-through; }

.CodeMirror .CodeMirror-placeholder {
  color: #777; }
